@media (min-width: 1000px) {
  html {
    font-size: 8px;
  }
}

@media (max-width: 800px) {
  .container {
    width: 750px;
    row-gap: 8rem;
  }
}

@media (max-width: 500px) {
  .header-flex {
    padding: 0;
  }
  .header-flex::before {
    z-index: 0;
  }

  .bio__pic {
    max-width: 100%;
  }

  .burger-menu {
    display: flex;
    flex-direction: column;
    cursor: pointer;
    align-self: center;
  }
  .burger-menu__bar {
    width: 20px;
    height: 2px;
    background-color: var(--color-text);
    margin-top: 3px;
    z-index: 0;
  }
  .header-flex:has(.menu__checkd:checked) .burger-menu__bar:nth-child(1) {
    transform: translatey(10px) rotate(40deg);
  }
  .header-flex:has(.menu__checkd:checked) .burger-menu__bar:nth-child(2) {
    opacity: 0;
  }
  .header-flex:has(.menu__checkd:checked) .burger-menu__bar:nth-child(3) {
    transform: translateX(0px) rotate(-40deg);
  }
  .navgition {
    position: absolute;
    width: 100vw;
    font-size: 12px;
    background-color: var(--color-background);
    top: 0px;
    padding: 2rem 0px;
    border-bottom: 1px solid var(--color-text);
    z-index: -1;
    transition: 0.3s;
  }
  .navgition__list {
    justify-content: center;
  }
  .menu__checkd:checked + .navgition {
    top: 100%;
  }
  .toggel {
    padding: 2rem 0;
    z-index: 1;
  }
}
